<template>
  <div class="big-table-wrap" ref="tableWrap">
    <div class="search-wrap" ref="searchWrap">
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">买方消费者：</span>
            <Input v-model="buyComsumer" placeholder="买方消费者" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">卖方消费者：</span>
            <Input v-model="saleComsumer" placeholder="卖方消费者" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">成交流水号：</span>
            <Input v-model="turnOverID" placeholder="成交流水号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">买方流水号：</span>
            <Input v-model="buyPublishID" placeholder="买方发布流水号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">卖方流水号：</span>
            <Input v-model="sellPublishID" placeholder="卖方发布流水号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">中间积分：</span>
            <Input v-model="middlePointsID" placeholder="中间积分ID" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">普通积分：</span>
            <Input v-model="generalPointsID" placeholder="普通积分ID" class="input-width"></Input>
          </div>
        </Col>
        <Col span="10" v-show="showMore">
          <div  class="input-item">
            <daterange :name="'成交日期'" ref="regTime"  @start-time="timeChange('okStart',$event)"  @end-time="timeChange('okEnd',$event)"></daterange>
           </div>
        </Col>
        <Col span="2">
          <div  class="input-item">
            <Button type="primary" @click="reSearchInfo">查询</Button>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="resetSearch" >重置</Button>
          </div>
        </Col>
      </Row>
      <showbtn :sendNum="10" @show-hide="showMore=$event"></showbtn>
    </div>
    <!--操作按钮区域-->
    <div class="handle-wrap" ref="handleWrap">
      <div class="handle-btn-wrap">
        <Button type="primary" @click="exportInfo">导出</Button>
      </div>
    </div>
    <!--table表格区域-->
    <div class="table-wrap-style" ref="tableBox">
      <Table  :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns" stripe border></Table>
    </div>
    <div class="page-wrap" ref="pageWrap">
      <div class="right">
        <Page :total="total" :current="page" @on-change="changePage" @on-page-size-change="changeSize" show-total show-sizer :page-size-opts="pageSizeOpts"></Page>
      </div>
    </div>
  </div>
</template>
<script>
  import { tableMethods } from "../../../js/mixintable";
  import daterange from '../../components/daterange'
  import showbtn from '../../components/showbtn'
  export default {
    name: "ExchangeTurnOverQuery",//互换成交
    data () {
      return {
        /*搜索区域 条件*/
        buyComsumer:'', //买方消费者
        saleComsumer:'', //卖方消费者
        turnOverID:'', //成交流水号
        buyPublishID:'', //买方发布流水号
        sellPublishID:'', //卖方发布流水号
        middlePointsID:'',//中间积分ID
        generalPointsID:'',//普通积分ID
        okStartTime:'', //成交开始时间
        okEndTime:'', //成交结束时间
        loading:false,
        /*table表格*/
        tableColumns: [
          {
            title: '成交流水号',
            key: 'deal_seq_no',
            align: 'center',
            minWidth: 150,
          },
          {
            title: '买方消费者',
            key: 'buy_user_no',
            align: 'center',
            minWidth: 150,
          },
          {
            title: '卖方消费者',
            key: 'sell_user_no',
            align: 'center',
            minWidth: 150,
          },
          {
            title: '买方流水号',
            key: 'buy_seq_no',
            align: 'center',
            minWidth: 150,
          },
          {
            title: '卖方流水号',
            align: 'center',
            key: 'sell_seq_no',
            minWidth: 150,
          },
          {
            title: '中间积分ID',
            align: 'center',
            key: 'buy_swap_cv',
            minWidth: 100,
          },
          {
            title: '中间积分',
            align: 'center',
            key: 'buy_swap_name',
            minWidth: 100,
          },
          {
            title: '中间积分数量',
            align: 'center',
            key: 'buy_swap_amount',
            minWidth: 120,
          },
          {
            title: '普通积分ID',
            align: 'center',
            key: 'buy_point_cv',
            minWidth: 100,
          },
          {
            title: '普通积分',
            align: 'center',
            key: 'buy_point_name',
            minWidth: 100,
          },
          {
            title: '普通积分数量',
            align: 'center',
            key: 'buy_amount',
            minWidth: 120,
          },
          {
            title: '成交时间',
            align: 'center',
            key: 'deal_time',
            minWidth: 200,
          }
        ]
      }
    },
    components: {
      daterange,showbtn
    },
    mixins: [tableMethods],
    mounted() {
      this.$nextTick(function () {
        //查询数据
       // this.searchInfo();
        //dom更新了，重新设置table的高度
         })
    },
    methods: {
      //查询数据
      searchInfo(){
        let that=this
        this.loading=true;
        /*buy_user_no 买方用户号
          sell_user_no 卖房用户号
          deal_serial_no 成交流水号
          buy_serial_no 买方流水号
          sell_serial_no 卖方流水号
          point_name 中间积分
          swap_name 普通积分
          start_deal_time 开始时间
          end_deal_time 结束时间*/
         /* deal_seq_no	成交流水号
        buy_user_no	买方消费者
        sell_user_no	卖方消费者
        buy_seq_no	买方流水号
        sell_seq_no	卖方流水号
        deal_time	成交时间
        buy_point_cv	中间积分ID
        buy_point_name	中间积分
        buy_amount	中间积分数量
        sell_point_cv	普通积分ID
        sell_point_name	普通积分
        sell_amount	普通积分数量
        */
        let objData={
          buy_user_no:this.buyComsumer,
          sell_user_no:this.saleComsumer,
          deal_serial_no:this.turnOverID,
          buy_serial_no:this.buyPublishID,
          sell_serial_no:this.sellPublishID,
          point_name:this.generalPointsID,
          swap_name:this.middlePointsID,

          min_deal_time:this.okStartTime.length>0?this.okStartTime.replace(/-/g,''):'' ,
          max_deal_time:this.okEndTime.length>0?this.okEndTime.replace(/-/g,''):'',
          pPage:this.page,
          pSize:this.size
        }
        let jsonData = protocol.getJsonParams(protocol.getAllDeal, objData);
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.$Loading.finish();
          let resultData=[]
          /*添加序号*/
          if(data.returnList&&data.returnList.length>0){
            data.returnList.forEach(function (item,index) {
              item.indexs=that.page*that.size+index-(that.size-1)
              resultData.push(item)
            })
          }
          this.tableData = resultData;
          this.total =data.total-0;
        })
      },
      //重置查询条件
      resetSearch(){
        /*搜索区域 条件*/
        this.buyComsumer=''// 账号
        this.saleComsumer='' //唯一编码
        this.turnOverID=''//状态
        this.buyPublishID=''//邀请码
        this.sellPublishID='' //邀请用户账号
        this.$refs.regTime.resetTime()
        this.generalPointsID='' //注册开始时间
        this.middlePointsID='' //注册结束时间
        this.okStartTime='' //实名开始时间
        this.okEndTime='' //实名结束时间
      },
      //获取时间变化并赋值
      timeChange(e,time){
        if(e=="okStart"){
          this.okStartTime=time
        }else if(e=="okEnd"){
          this.okEndTime=time
        }
      },
      //导出
      exportOk () {
        let objData={
          buy_user_no:this.buyComsumer,
          sell_user_no:this.saleComsumer,
          deal_serial_no:this.turnOverID,
          buy_serial_no:this.buyPublishID,
          sell_serial_no:this.sellPublishID,
          point_name:this.generalPointsID,
          swap_name:this.middlePointsID,
          min_deal_time:this.okStartTime.length>0?this.okStartTime.replace(/-/g,''):'' ,
          max_deal_time:this.okEndTime.length>0?this.okEndTime.replace(/-/g,''):'',
        }
        let jsonData = protocol.getJsonParams(protocol.exportAllDeal, objData);
        http.ajax('', jsonData, true, (data) => {
          window.location.href=loadUrl+data.excel_url
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import '../../../css/common.css';
</style>




